body{
    div{
        background-color:skyblue;
        p{
            font-size:30px;
            font-weight: 900;
        }
    }
}

.bg-brown{
    background-color:brown;
}

.bg-white{
    background-color:#fff;
}

.bg-black{
    background-color:black;
}
.w_bgb{
    .area{
        min-height: 500px; 
        background-attachment: fixed;
        background-position: center center;
        background-size: cover;
    }
}

.bigbox{
    height:100vh;
    width:100%;
    .article{
        background-color:black;
        width:100%;
        margin: 0 auto;
        height: 100vh;
        padding: 0px;
        
        background-repeat: no-repeat;
        background-attachment: fixed;
        background-position: center center;
        background-size: cover;
        
    }
    .section1{
        background-image:url("https://images.pexels.com/photos/289324/pexels-photo-289324.jpeg");
    }
    .section2{
        background-image:url("https://images.pexels.com/photos/34809/loro-park-orca-fish.jpg");
    }
    .section3{
        background-image:url("https://images.pexels.com/photos/1071385/pexels-photo-1071385.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260");
    }
    
}

