body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body div {
  background-color: skyblue; }
  body div p {
    font-size: 30px;
    font-weight: 900; }

.bg-brown {
  background-color: brown; }

.bg-white {
  background-color: #fff; }

.bg-black {
  background-color: black; }

.w_bgb .area {
  min-height: 500px;
  background-attachment: fixed;
  background-position: center center;
  background-size: cover; }

.bigbox {
  height: 100vh;
  width: 100%; }
  .bigbox .article {
    background-color: black;
    width: 100%;
    margin: 0 auto;
    height: 100vh;
    padding: 0px;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center center;
    background-size: cover; }
  .bigbox .section1 {
    background-image: url("https://images.pexels.com/photos/289324/pexels-photo-289324.jpeg"); }
  .bigbox .section2 {
    background-image: url("https://images.pexels.com/photos/34809/loro-park-orca-fish.jpg"); }
  .bigbox .section3 {
    background-image: url("https://images.pexels.com/photos/1071385/pexels-photo-1071385.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260"); }

